
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.list {
  background: #fff;
}
.list .head-list {
  margin-bottom: 0;
  padding: 20px 0 15px 10px;
  border-bottom: 1px solid #e6e6e6;
}
.list .head-list li {
  margin-right: 30px;
  display: inline-block;
  list-style: none;
  font-size: 16px;
}
.list .head-list li a {
  color: #333;
  text-decoration: none;
}
.list .head-list li a.active {
  padding-bottom: 15px;
  border-bottom: 3px solid #007bff;
  color: #007bff;
}
.list .article-list {
  padding: 0;
}
.list .article-list li {
  position: relative;
  list-style: none;
  height: auto;
  overflow: hidden;
  padding: 10px;
  border-bottom: 1px solid #e6e6e6;
}
.list .article-list li .avatar {
  margin-bottom: 10px;
}
.list .article-list li .avatar a {
  text-decoration: none;
  color: #333;
}
.list .article-list li .avatar a img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
.list .article-list li .avatar a .nick {
  display: inline-block;
  font-size: 14px;
  font-weight: bold;
}
@media screen and (max-width: 768px) {
.list .article-list li .avatar a img {
    width: 35px;
    height: 35px;
    vertical-align: -12px;
}
}
.list .article-list li .avatar .time {
  margin-left: 10px;
  color: #a5a5a5;
}
.list .article-list li .info .img-outer {
  float: left;
  width: 150px;
  height: 97px;
  margin-right: 15px;
  background-color: #dde6ec;
  background-image: url(https://oss02.bihu.com/img/bihu_user_default_icon.png?x-oss-process=style/size_head);
  background-size: 50px 50px;
  background-position: center center;
  background-repeat: no-repeat;
}
.list .article-list li .info .img-outer img {
  width: 100%;
  height: 100%;
}
.list .article-list li .info .title {
  margin-top: 10px;
  padding-top: 2px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #333;
}
.list .article-list li .info .title h3 {
  margin-top: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.list .article-list li .info .title .summary {
  color: #777;
}
.list .article-list li .info .thumbs {
  position: absolute;
  bottom: 8px;
  left: 175px;
  margin: 0;
}
.list .article-list li .info .thumbs > span {
  margin-right: 20px;
  color: #777;
}
.list .article-list li .info .thumbs > span i {
  margin-right: 3px;
}
.list .article-list li .info .thumbs > span i.glyphicon-comment {
  vertical-align: -1px;
}
@media screen and (max-width: 768px) {
.list .article-list li .info .img-outer {
    width: 120px;
    height: 66px;
    margin-right: 10px;
}
.list .article-list li .info .title h3 {
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: bold;
}
.list .article-list li .info .summary {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.list .article-list li .info .thumbs {
    left: 145px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.list .article-list li .info .thumbs > span {
    margin-right: 6px;
    font-size: 12px;
}
.list .article-list li .info .thumbs > span i {
    margin-right: 1px;
}
}

.navbar {
  width: 100%;
  background: #444;
}
.navbar .navbar-toggle {
  margin-right: -15px;
}
.navbar .navbar-toggle .icon-bar {
  background: #fff;
}
.navbar .navbar-inner {
  background: 0 0;
  border-radius: 0;
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  width: 90%;
  margin: auto;
  padding: 5px;
}
.navbar .navbar-inner .brand {
  display: inline-block;
  width: 110px;
  padding: 5px 0;
}
.navbar .navbar-inner .brand img {
  width: 100%;
}
.navbar .navbar-inner .nav {
  padding-left: 35px;
}
.navbar .navbar-inner .nav li {
  position: relative;
}
.navbar .navbar-inner .nav li a {
  color: #ccc;
  font-size: 16px;
}
.navbar .navbar-inner .nav li a:hover {
  background: #444;
  color: #fff;
}
.navbar .navbar-inner .nav li a.active {
  color: #fff;
}
.navbar .navbar-inner .nav li .avatar {
  width: 30px;
  height: 30px;
  margin-top: 5px;
  padding: 0;
}
.navbar .navbar-inner .nav li .avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
@media screen and (max-width: 768px) {
.navbar-collapse {
    position: fixed;
    top: 60px;
    right: 0;
    width: calc(35% + 15px);
    height: calc(100vh - 60px);
    max-height: 100vh !important;
    background: #444;
}
}

.category-menu {
  position: absolute;
  top: 61px;
  left: -45px;
  width: 620px;
  padding: 20px 0 5px;
  background: #fff;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
          box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
}
.category-menu:before {
  position: absolute;
  top: -10px;
  left: 65px;
  content: "";
  width: 20px;
  height: 20px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  background: #fff;
  border: solid #fff;
  border-width: 1px 0px 0 1px;
}
.category-menu ul {
  padding: 0;
}
.category-menu ul li {
  float: left;
  margin: 0 0 15px 20px;
  text-align: center;
  list-style: none;
}
.category-menu ul li a {
  display: block;
  width: 100px;
  height: 35px;
  line-height: 35px;
  background: #ebf3fb;
  text-decoration: none;
  color: #95b5d7 !important;
  font-size: 14px !important;
}
.category-menu ul li a:hover {
  color: #fff !important;
}
.category-menu ul li a.router-link-exact-active {
  background: #444;
  color: #fff !important;
}
@media screen and (max-width: 768px) {
.category-menu {
    position: fixed;
    right: 50%;
    left: 0;
    width: 65%;
    padding-top: 10px;
}
.category-menu:before {
    position: fixed;
    top: 118px;
    left: 62%;
}
.category-menu ul {
    height: calc(100vh - 60px);
    overflow-y: auto;
    padding-bottom: 10px;
}
.category-menu ul li {
    width: 28%;
    margin: 0 0 10px 10px;
}
.category-menu ul li a {
    width: 100%;
}
}

.copyright {
  padding: 10px 0 20px;
  text-align: center;
}

.pagination {
  overflow: hidden;
  display: table;
  margin: 10px auto;
  padding: 15px 0 25px;
}
.pagination li {
  float: left;
  height: 30px;
  margin: 3px;
  border-radius: 5px;
}
.pagination li a {
  display: block;
  height: 34px;
  text-align: center;
  font-size: 14px;
  border-radius: 3px;
  text-decoration: none;
}

